.details {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 50px 0;

  img {
    max-width: 500px;
    min-width: 290px;
    height: 300px;
    width: 100%;
    display: block;
    object-fit: cover;
    margin: 25px;
  }

  .box {
    max-width: 500px;
    min-width: 290px;
    margin: 25px;

    .row {
      display: flex;
      justify-content: space-between;
      margin-bottom: 15px;

      h2 {
        text-transform: uppercase;
        letter-spacing: 2px;
      }
    }

    .colors {
      button {
        width: 30px;
        height: 30px;
        border: 1px solid #333;
        outline: none;
        margin-right: 5px;
        cursor: pointer;
      }
    }

    p {
      line-height: 1.5;
      margin: 15px 0;
    }

    .cart {
      background: #333;
      color: white;
      text-decoration: none;
      padding: 10px 25px;
      display: inline-block;
    }
  }
}
